/**************************
	drawer
*/
.mm-page {
  --mm-size: 0%;
  --mm-max-size: 0%;
}

.mm-menu--fullscreen.mm-menu--opened ~ .mm-slideout {
  --mm-max-size: 0%;
}

.mm-wrapper--opened .mm-slideout {
  transform: none !important;
}

body.mm-wrapper--opened .root .header {
  background-color: #ffffff;
}
body.mm-wrapper--opened .root .header__inner {
  padding: 10px 20px 10px 18px;
}

#drawer {
  width: 100%;
  top: 0;
  z-index: 1000;
  box-shadow: 2px 2px 2px 2px #6f859b;
}
#drawer .drawer__bottom {
  flex-direction: column !important;
}
#drawer .contact_btn {
  padding-top: 20px;
  padding-bottom: 20px;
}
#drawer .contact_btn a {
  width: 188px;
  display: block;
  background-color: #66acac;
  padding: 8px 10px;
  text-align: center;
  box-sizing: border-box;
  border: 1px solid transparent;
  transition: background-color 0.2s;
  margin: 0 auto;
}
#drawer .contact_btn a:hover {
  background-color: white;
  border: 1px solid #66acac;
}
#drawer .contact_btn a:hover .text {
  color: #66acac;
}
#drawer .contact_btn a:hover .text::before {
  background-position-y: -16px;
}
#drawer .contact_btn a .text {
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  letter-spacing: 0.1em;
}
#drawer .contact_btn a .text::before {
  content: "";
  display: inline-block;
  background: url("../img/icon/mail_white.webp") 0 0 no-repeat transparent;
  background-size: 23px 31px;
  width: 23px;
  height: 16px;
  margin-right: 10px;
}

.mm-navbar {
  height: 60px;
  border-bottom: 1px solid #728397;
  background: transparent;
  background-size: cover;
  background-color: #ffffff;
}
.mm-navbar__title {
  justify-content: flex-start;
  padding-left: 0 !important;
}
.mm-navbar__title > span {
  color: #2c3746;
  font-size: 1.125rem;
}
.mm-navbar__btn {
  width: 50px;
}
.mm-navbar__btn::before {
  font-size: 40px;
  font-weight: 400;
  margin-right: 8px;
  color: #ed8767;
}

.mm-panel {
  background-color: #ffffff;
  padding: 0 20px;
}
.mm-panel * {
  color: #212a37;
}
.mm-panel::after {
  display: none;
}

#mm-1 .mm-navbar {
  display: none;
}

.mm-listitem {
  border-color: #ffffff;
  padding: 0;
  font-size: 1rem;
}
.mm-listitem:last-of-type::after {
  content: none;
}
.mm-listitem:nth-last-child(-n+1)::after {
  border-bottom: none;
  left: 0;
  width: 100%;
  height: 1px;
  background: #ed8767;
}
.mm-listitem__text {
  font-weight: 700;
}
.mm-listitem__text > ._inner {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.mm-listitem__text::after {
  border-color: #ed8767;
}
.mm-listitem .mm-listview > .mm-listitem:not(:last-of-type) {
  margin-bottom: 6px;
}
.mm-listitem.li1, .mm-listitem.li2, .mm-listitem.li3, .mm-listitem.li4, .mm-listitem.li5 {
  border-radius: 5px;
  border-bottom: 1px solid #ed8767;
}
.mm-listitem.li1::after, .mm-listitem.li2::after, .mm-listitem.li3::after, .mm-listitem.li4::after, .mm-listitem.li5::after {
  display: none;
}
.mm-listitem.li1 .mm-listitem__text, .mm-listitem.li2 .mm-listitem__text, .mm-listitem.li3 .mm-listitem__text, .mm-listitem.li4 .mm-listitem__text, .mm-listitem.li5 .mm-listitem__text {
  padding: 20px 30px;
  position: relative;
  background-color: transparent;
}
.mm-listitem::after {
  border-bottom: 1px solid #ed8767;
  left: 0;
  z-index: 1;
}
.mm-listitem--opened {
  padding-bottom: 15px !important;
}
.mm-listitem--opened .mm-listitem__btn {
  background-color: #ffffff;
}
.mm-listitem--opened .mm-listitem__btn::after {
  opacity: 0 !important;
}
.mm-listitem--opened .mm-panel {
  background-color: #ffffff;
  padding-right: 10px;
  padding-left: 10px;
  padding-top: 0;
}

.mm-listitem__btn:not(.mm-listitem__text) {
  border: none;
  position: relative;
  height: 60px;
}
.mm-listitem__btn:not(.mm-listitem__text)::after {
  content: "";
  display: inline-block;
  background: url("../img/icon/arrow/arrow_menu.webp") 0 0 no-repeat transparent;
  background-size: 7px 10px;
  width: 7px;
  height: 10px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.mm-wrapper__blocker {
  background: rgba(0, 0, 0, 0);
}

.mm-menu--offcanvas {
  transition: 0.3s cubic-bezier(0.16, 0.96, 0.25, 0.91);
}
.mm-menu--offcanvas:not(.mm-menu--opened) {
  transform: translateX(100%);
}
.mm-menu--offcanvas.mm-menu--opened {
  transform: translateX(0);
}

.drawer__li {
  padding: 0;
}
.drawer__li:last-of-type {
  margin-bottom: 40px;
}

.drawer__a {
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  position: relative;
  color: #4d4741 !important;
  letter-spacing: 0.1em;
}
.drawer__a.ext {
  font-family: "Zen Kaku Gothic Antique", sans-serif;
  font-weight: 500;
  color: #535353 !important;
  font-size: 1rem;
  padding-left: 36px;
}
.drawer__a.ext::before {
  display: block;
  content: "";
  background: url("../img/icon/ext_2.webp") no-repeat;
  background-size: 19px 31px;
  width: 19px;
  height: 16px;
  position: absolute;
  top: 30%;
  left: 8px;
}
.drawer__a:hover {
  opacity: 0.7;
}

.sp_menu_area {
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 0;
}

.drawer__search {
  border-radius: 20px;
  max-width: 335px;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
  padding: 0;
}
.drawer__search > .search_input .link {
  color: #ffffff !important;
}
.drawer__search > .search_input .link::before {
  background: url("../img/icon/arrow/link_arrow_white.webp") no-repeat;
  background-size: contain;
}
.drawer__search > .search_input .input {
  background-color: #f1f0f0 !important;
  color: #aeaeae !important;
}
.drawer__search > .search_input .input::placeholder {
  color: #aeaeae;
}
.drawer__search > .search_input .submit_btn {
  font-size: 0.938rem;
  font-weight: 500;
  letter-spacing: 0.05em;
  display: block;
  width: 55px;
  height: 100%;
  position: absolute;
  margin: 0 0 0 auto;
  border: none;
  outline: none;
  color: #ffffff;
  background-color: #293751;
  z-index: 50;
  right: 0;
}

.mm-navbars--bottom {
  border-top: none;
}